.App {
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
  margin-bottom: 2rem;
}

.no-puzzles-container, .no-results-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  max-width: 400px;
  margin: 40px auto;
  text-align: center;
}

.no-puzzles-icon, .no-results-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  opacity: 0.7;
}

.no-puzzles-container h3, .no-results-container h3 {
  color: #333;
  margin: 0 0 12px 0;
  font-size: 1.3rem;
  font-weight: 600;
}

.no-puzzles-container p, .no-results-container p {
  color: #666;
  margin: 0 0 24px 0;
  font-size: 1rem;
  line-height: 1.4;
}

.filter-suggestions {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  justify-content: center;
}

.suggestion-button {
  background: #007acc;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.suggestion-button:hover {
  background: #005a99;
}

.Loading {
  display: flex;
  justify-content: center;
  height: 100%;
  flex: 1;
  margin-bottom: -10px;
}

.chessboard-info {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 400px;
  align-items: center;
  margin-bottom: 0.5rem;
}

.level-display {
  text-align: left;
  font-size: 1.2em;
  padding: 3px 0;
  color: #2f74ab;
  text-align: left;
  margin: 2px 0;
  font-weight: bold;
}


.chessboard-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  box-sizing: border-box;
}

.evaluation-container {
  margin-top: 1rem;
}