.chessboard-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
  
    overflow: hidden;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    position: relative;
    aspect-ratio: 1;
  }
  
  .chessboard-wrapper > div {
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    width: 100% !important;
    height: 100% !important;
  }

  /* Integrated ChessBase-style board */
  .chessboard-wrapper.integrated {
    border-bottom: none;
    overflow: hidden;
  }

  .chessboard-wrapper.integrated > div {
    border: none;
    border-radius: 0;
  }

  .chessboard-wrapper > div > div {
    width: 100% !important;
    height: 100% !important;
  }

  /* Integrated controls container */
  .chessboard-integrated-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  }

  .chessboard-integrated-container .chessboard-wrapper {
    border-radius: 0;
    border: none;
    box-shadow: none;
    margin: 0;
  }
 