.UserInfoSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 16px;
  padding: 32px 20px;
  border-bottom: 1px solid #eee;
}

.UserAvatarLarge {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #2f74ab;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  font-weight: 600;
  color: white;
}

.UserDetailsSection {
  h2 {
    margin: 0 0 8px 0;
    font-size: 24px;
    color: #333;
    font-weight: 600;
  }
}

.Username {
  margin: 0 0 12px 0;
  font-size: 16px;
  color: #666;
}

.RoleBadge {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;

  &.RoleAdmin {
    background-color: #e74c3c;
    color: white;
  }

  &.RoleUser {
    background-color: #3498db;
    color: white;
  }

  &.RoleGuest {
    background-color: #95a5a6;
    color: white;
  }
}

.ProfileNavigation {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 32px;
}

.NavItemProfile {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px 20px;
  cursor: pointer;
  border-radius: 12px;
  transition: all 0.2s ease;
  background: #f8f9fa;
  border: 1px solid transparent;

  &:hover {
    background: #e9ecef;
    border-color: #2f74ab;
    transform: translateY(-1px);
  }
}

.NavIconProfile {
  width: 24px;
  height: 24px;
  fill: #2f74ab;
  flex-shrink: 0;
}

.NavLabelProfile {
  font-size: 16px;
  font-weight: 500;
  color: #333;
}

.LogoutBtn {
  padding: 16px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  margin: 0 20px;

  &:hover {
    background: #c82333;
    transform: translateY(-1px);
  }
}