.StatisticsPage {
  max-width: 400px;
  margin: 0 auto;
  width: 100%;
}

.StatsHeader {
  text-align: center;
  margin-bottom: 2rem;

  h1 {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 0.5rem;
    font-weight: 600;
  }
}

.RankBadge {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(135deg, #2f74ab 0%, #1e5a8a 100%);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 12px;
  margin: 0 auto;
  max-width: 200px;
}

.RankText {
  font-weight: 600;
  font-size: 0.9rem;
}

.XpText {
  font-size: 0.8rem;
  opacity: 0.9;
}

.OverallStats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 2rem;
}

.StatCard {
  background: white;
  border-radius: 12px;
  padding: 1rem;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #f0f0f0;
}

.StatValue {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2f74ab;
  margin-bottom: 0.25rem;
}

.StatLabel {
  font-size: 0.8rem;
  color: #666;
  font-weight: 500;
}

.AchievementsSection {
  margin-bottom: 2rem;

  h2 {
    font-size: 1.2rem;
    color: #333;
    margin-bottom: 1rem;
    font-weight: 600;
  }
}

.AchievementsGrid {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.Achievement {
  display: flex;
  align-items: center;
  background: white;
  border-radius: 12px;
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #f0f0f0;
}

.AchievementIcon {
  font-size: 2rem;
  margin-right: 1rem;
}

.AchievementText {
  flex: 1;
}

.AchievementTitle {
  font-size: 0.9rem;
  color: #333;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.AchievementValue {
  font-size: 0.8rem;
  color: #666;
}

.ThemeStatsSection {
  h2 {
    font-size: 1.2rem;
    color: #333;
    margin-bottom: 1rem;
    font-weight: 600;
  }
}

.ThemeStatsGrid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.ThemeCard {
  background: white;
  border-radius: 12px;
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #f0f0f0;
}

.ThemeHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;

  h3 {
    font-size: 1rem;
    color: #333;
    margin: 0;
    font-weight: 600;
  }
}

.ThemeLevel {
  font-size: 0.9rem;
  color: #ffa726;
}

.ThemeProgress {
  margin-bottom: 1rem;
}

.ProgressBar {
  width: 100%;
  height: 8px;
  background-color: #f0f0f0;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 0.5rem;
}

.ProgressFill {
  height: 100%;
  background: linear-gradient(90deg, #2f74ab 0%, #4caf50 100%);
  border-radius: 4px;
  transition: width 0.3s ease;
}

.ProgressText {
  font-size: 0.8rem;
  color: #666;
  text-align: center;
}

.ThemeMetrics {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.Metric {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.MetricLabel {
  font-size: 0.8rem;
  color: #666;
}

.MetricValue {
  font-size: 0.8rem;
  font-weight: 600;
  color: #333;

  &.SuccessRateGreen {
    color: #4caf50;
  }

  &.SuccessRateOrange {
    color: #ff9800;
  }

  &.SuccessRateRed {
    color: #f44336;
  }
}

.EmptyState {
  text-align: center;
  padding: 2rem 1rem;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #f0f0f0;
  margin-bottom: 2rem;

  h2 {
    font-size: 1.3rem;
    color: #333;
    margin-bottom: 1rem;
    font-weight: 600;
  }

  p {
    color: #666;
    line-height: 1.5;
    margin-bottom: 1.5rem;
    font-size: 0.9rem;
  }
}

.EmptyIcon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.EmptyProgress {
  margin-bottom: 2rem;
}

.ProgressInfo {
  margin-bottom: 0.5rem;

  span {
    font-size: 0.8rem;
    color: #666;
    font-weight: 500;
  }
}

.ExampleButton {
  background: linear-gradient(135deg, #2f74ab 0%, #1e5a8a 100%);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(47, 116, 171, 0.3);
  }
}

.BackButton {
  background: #f5f5f5;
  color: #333;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 0.8rem;
  cursor: pointer;
  margin-left: 1rem;
  transition: background-color 0.2s ease;

  &:hover {
    background: #e0e0e0;
  }
}

.ExampleBanner {
  background: linear-gradient(135deg, #ffa726 0%, #ff8f00 100%);
  color: white;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  text-align: center;
  font-size: 0.8rem;
  font-weight: 500;
}

@media (max-width: 380px) {
  .OverallStats {
    grid-template-columns: 1fr;
  }
  
  .StatValue {
    font-size: 1.3rem;
  }
  
  .AchievementIcon {
    font-size: 1.5rem;
    margin-right: 0.75rem;
  }

  .EmptyState {
    padding: 1.5rem 1rem;
  }

  .EmptyIcon {
    font-size: 2.5rem;
  }

  .EmptyState h2 {
    font-size: 1.2rem;
  }

  .BackButton {
    margin-left: 0;
    margin-top: 0.5rem;
    display: block;
  }
}