.LoginContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: white;
  padding: 2rem;
  box-sizing: border-box;
}

.WelcomeMessage {
  color: #000;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 3rem;
  max-width: 400px;
}

.LoginForm {
  background: transparent;
  padding: 0;
  border-radius: 0;
  box-shadow: none;
  width: 100%;
  max-width: 400px;
}

.LoginLogo {
  text-align: center;
  margin-bottom: 1rem;

  .LogoImg {
    height: 80px;
    width: auto;
    object-fit: contain;
  }
}

.FormGroup {
  margin-bottom: 1rem;
}

.FormLabel {
  display: block;
  margin-bottom: 0.5rem;
  color: #555;
  font-weight: 500;
}

.FormInput {
  width: 100%;
  padding: 1rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
  box-sizing: border-box;
  background: white;
  transition: border-color 0.2s, box-shadow 0.2s;

  &:focus {
    outline: none;
    border-color: #2f74ab;
    box-shadow: 0 0 0 2px rgba(47, 116, 171, 0.2);
  }
}

.LoginButton {
  width: 100%;
  padding: 1rem;
  background-color: #2f74ab;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  margin-bottom: 1rem;
  transition: background-color 0.2s, transform 0.1s;

  &:hover:not(:disabled) {
    background-color: #255d89;
    transform: translateY(-1px);
  }

  &:disabled {
    background-color: #bdc3c7;
    cursor: not-allowed;
  }
}

.GuestLinkContainer {
  text-align: center;
  margin-top: 1.5rem;
}

.GuestLink {
  background: none;
  border: none;
  color: #2f74ab;
  font-size: 14px;
  cursor: pointer;
  text-decoration: underline;
  padding: 0.5rem;
  transition: color 0.2s;

  &:hover:not(:disabled) {
    color: #255d89;
  }

  &:disabled {
    color: #bdc3c7;
    cursor: not-allowed;
  }
}

.ForgotPasswordLink {
  display: block;
  width: 100%;
  text-align: right;
  margin-top: 0.5rem;
  margin-bottom: 1.5rem;
  color: #2f74ab;
  font-size: 14px;
  background: none;
  border: none;
  padding: 0;
  text-decoration: underline;
  cursor: pointer;
  font-family: inherit;

  &:hover {
    color: #255d89;
  }
}

.ErrorMessage {
  color: #e74c3c;
  text-align: center;
  margin-bottom: 1rem;
  padding: 1rem;
  background-color: #fdf2f2;
  border: 1px solid #fecaca;
  border-radius: 8px;
  font-weight: 500;
}